.footer {
  background-color: var(--bright-gray);
}

.footer__container {
  display: flex;
  align-items: center;
  padding-top: 30px;
  padding-bottom: 30px;
}

.footer__paragraph {
  margin: 0;
  font-size: 13px;
  line-height: 16px;
  color: var(--white-color);

  & span {
    text-transform: uppercase;
  }

  & br {
    display: none;
  }
}

.footer__payment {
  display: flex;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
}

.footer__payment-item {
  position: relative;
  padding-left: 30px;
  font-size: 13px;
  line-height: 16px;
  color: var(--silver-3);

  &::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    width: var(--width);
    height: var(--height);
    background-position: center;
    background-size: var(--width) var(--height);
    background-repeat: no-repeat;
    transform: translateY(-50%);
  }

