.footer__contacts {
  display: flex;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
}

.footer__contacts-link {
  position: relative;
  display: inline-block;
  padding-left: 30px;
  font-size: 13px;
  line-height: 16px;
  color: var(--white-color);

  &::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    width: var(--width);
    height: var(--height);
    background-position: center;
    background-size: var(--width) var(--height);
    background-repeat: no-repeat;
    transform: translateY(-50%);
  }

