.footer__delimiter {
  display: none;
  margin: 0 10px;
  width: 1px;
  height: 18px;
  background-color: var(--silver-3);
}

@media (min-width: 1023.98px) AND (hover) {

  .footer__contacts-link {
    transition: color .2s var(--transition-main);
  }

  .footer__contacts-link:hover {
    color: var(--havelock-blue);
  }
}

@media (min-width: 1099.98px) {

  .footer__copyright {
    margin-right: auto;
  }

  .footer__payment {
    margin-left: auto;
    margin-right: auto;
  }

  .footer__contacts {
    margin-left: auto;
  }

  .footer__delimiter {
    display: block;
  }
}

@media (max-width: 1099.98px) {

  .footer__container {
    flex-direction: column;
  }

  .footer__copyright {
    margin-bottom: 40px;
    text-align: center;
  }
  .footer__payment {
    margin-bottom: 25px;
  }
}

@media (min-width: 479.98px) {

  .footer__payment-item + .footer__payment-item {
    margin-left: 38px;
  }

  .footer__contacts-item + .footer__contacts-item {
    margin-left: 38px;
  }
}

@media (max-width: 479.98px) {

  .footer__payment,
  .footer__contacts {
    flex-direction: column;
  }

  .footer__payment {
    margin-bottom: 40px;
}

  .footer__payment-item + .footer__payment-item {
    margin-top: 18px;
  }

  .footer__contacts-item + .footer__contacts-item {
    margin-top: 18px;
  }
}
